.form__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 85px;

  &:last-child {
    margin-bottom: 0;
  }
}

.form__fieldset {
  @include indents-none;
  box-sizing: border-box;
  border: none;
}

.form__label {
  @include indents-none;
  @include font-weight($medium);
  display: block;
  margin-bottom: 23.5px;
  font-size: 22px;
  line-height: 28px;
}

.form__inputs {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.form__input {
  @include indents-none;
  box-sizing: border-box;
  display: block;
  min-height: 60px;
  font: inherit;
  line-height: 34px;
  color: $input-text;
  outline: none;
  background-color: transparent;
  border: 1px solid $main-text;
  border-radius: 4px;

  &--number,
  &--calendar {
    width: 390px;
    font-size: 24px;

    &:focus {
      box-shadow:inset 0 0 2px 1px $button;
    }
  }

  &--number {
    @include font-weight($medium);
    margin-right: 15px;
    padding: 11px 0;
    text-align: center;
    appearance: textfield;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      appearance: none;
    }
  }

  &--select {
    width: 120px;
    padding: 11.5px 13px;
    font-size: 30px;
    appearance: none;

    &:hover {
      cursor: pointer;
    }
  }

  &--calendar {
    padding: 6px 116px 0;

    &:hover {
      cursor: pointer;
    }
  }
}

.form__select,
.form__calendar {
  position: relative;
}

.form__to-save {
  width: 382px;
  min-height: 60px;
  font-size: 18px;
  line-height: 25px;
  padding-top: 1px;
}
